/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react'
import cx from 'classnames'

import Table from '../Table/StubbedTable'

import styles from './TransactionOrders.module.scss'

import OnDeal from '../../../images/svgs/onDeal.inline.svg'
import Transfer from '../../../images/svgs/transfer.inline.svg'
import CheckboxPenOutline from '../../../images/svgs/checkboxPenOutline.inline.svg'
import DownloadButton from '../../../images/svgs/downloadButton.inline.svg'
import Pdf from '../../../images/svgs/pdf.inline.svg'

import { table1 } from '../../../assets/table'


const RenderCell = cellInfo => (
    <div className={styles.table__cell}>
        {cellInfo.value}
    </div>
)
const RenderCellState = cellInfo => (
    <div className={cx([
        styles.table__cell,
        styles.table__cell_state,
        {
            [styles.table__cell_proved]: cellInfo.original.progress > 0,
        },
    ])}
    >
        {cellInfo.value}
    </div>
)
const RenderCellDoc = cellInfo => (
    <div className={styles.table__cell}>
        <a href="#">
            <Pdf />
            {cellInfo.value}
        </a>
    </div>
)

const Button = ({ children, ...props }) => (
    <button {...props} type="button">
        {children}
    </button>
)

const columns = [
    {
        Header: 'Дата создания',
        accessor: 'date', // String-based value accessors!
        Cell: RenderCell,
        minResizeWidth: 10,
    },
    {
        Header: 'Название документа',
        accessor: 'name', // String-based value accessors!
        Cell: RenderCellDoc,
        minResizeWidth: 10,
    },
    {
        Header: 'Статус',
        accessor: 'state', // String-based value accessors!
        Cell: RenderCellState,
        minResizeWidth: 10,
    },
    {
        Header: 'Дата исполнения',
        accessor: 'dateD',
        Cell: RenderCell,
        minResizeWidth: 10,
    },
    {
        Header: '',
        id: 'controls',
        width: 190,
        minResizeWidth: 10,
        accessor: d => (
            <div>
                <Button className={cx([styles.button_white, styles.button_width, styles.button])} type="button">
                    {d.action === 'Скачать' && <DownloadButton />}
                    {d.action}
                </Button>
            </div>
        ),
    },
]

const tableProps = {
    columns,
    data: table1,
    tableName: 'Поручения',
}


function TransactionOrders() {
    return (
        <section className={styles.TransactionOrder}>
            <div className={styles.controls}>
                Подать поручение:
                <div className={styles.controls__container}>
                    <button className={cx([styles.button, styles.controls__button])} type="button">
                        <OnDeal />
                        На сделку
                    </button>
                    <button className={cx([styles.button, styles.controls__button])} type="button">
                        <Transfer />
                        Перевод средств
                    </button>
                    <button className={cx([styles.button, styles.controls__button])} type="button">
                        <CheckboxPenOutline />
                        В свободной форме
                    </button>
                </div>
            </div>
            <br />
            <div className={styles.table}>
                <Table
                    {...tableProps}
                />
            </div>
        </section>
    )
}

export default TransactionOrders
