import React from 'react'
import ReactTable from 'react-table'

import './Table.scss'

const Table = ({
    columns, defaultPageSize, ...props
}) => {
    const defaultProps = {
        showPagination: true,
        resizable: true,
        // showPageSizeOptions: false,
        defaultPageSize: defaultPageSize || 5,
        pageSizeOptions: [5, 20, 50, 100],
        previousText: 'Назад',
        nextText: 'Вперед',
        loadingText: 'Загрузка данных...',
        noDataText: 'Нет данных',
        pageText: 'Страница',
        ofText: 'из',
        rowsText: 'Строк',
        ...props,
    }

    return (
        <ReactTable
            columns={columns}
            {...defaultProps}
        />
    )
}

export default Table
